@import url('https://fonts.googleapis.com/css2?family=Mulish:ital,wght@0,200..1000;1,200..1000&display=swap');



* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

h1 {
    text-align: center;
    margin-bottom: 2rem;
   
}

.alert {
    background: #dfdfdf;
}

body {
    font-size: 18px;
    font-family: "Mulish", sans-serif;
    display: flex;
    justify-content: stretch;
    align-items: stretch;
    flex-direction: column;
    padding: 5rem 2rem;
    padding-bottom: 10rem;
    padding-top: 2rem;
}

.developedBy {
    margin-bottom: 2rem;
}

.periodicTable {
    display: flex;
    justify-content: start;
    align-items: end;
    gap: .5rem;
    width: 100%;
    margin-bottom: 3rem;
    margin-top: 5rem;
}


.column {
    display: flex;
    flex-direction: column;
    gap: .5rem;
    justify-content: end;
    min-width: 4rem;
    align-items: stretch;
}

.column div {
    width: 100%;
    min-height: 4rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: #dfdfdf;
    color: #222;
}

.row {

    display: flex;
    gap: .5rem;
    justify-content: end;
    margin-bottom: .5rem;
    padding: 0;
}

.lanthanides-actinides {
    position: absolute;
    left: calc((2rem * 2) + (.5rem * 2) + (3rem * 5) );
    
}

.row div {
    min-width: 4rem;
    aspect-ratio: 1/1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: #dfdfdf;
    color: #222;

}

.column div:hover,
.row div:hover {
    transform: scale(1.3);
    cursor: pointer;
    box-shadow: 0 0 1rem #0009;
}
.column div span,
.row div span {
    font-size: .5em;
    font-weight: 700;
}

.column div b ,
.row div b {
    font-size: .6rem;
}

.column h2 {
    min-width: 200px;
    font-size: 1rem;
}


/* Styles Elements */

.alkaliMetal {
    background: #ff80df !important;
}

.alkalineEarthMetal {
    background: #80a2ff !important;
}


.metalloid {
    background: yellow !important;
}

.transition {
    background: #ffe880 !important;
}


.postTransitionMetal {
    background: #448b6a !important;
 
}

.nonmetal {
    background: #94843b !important;
 
}

.halogen {
    background: #9d79d6 !important;

}

.nobleGas {
    background: #4a8b44 !important;

}

@media screen and (max-width: 812px){
    h1 {
        font-size: 2rem ;
    }
}